@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@200;300;400;500;600&family=Fraunces:opsz,wght@9..144,400;9..144,500;9..144,600;9..144,700;9..144,800;9..144,900&display=swap');

body {
    margin: 0;
    background: #00001a;
    color: white;
}

.center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%)
}

h1 {
    font-size: 3em;
    font-family: 'Fraunces', serif;
    font-weight: 500;
}

p {
    font-size: 1.4em !important;
    font-family: 'Epilogue', sans-serif;
    font-weight: 400;
    line-height: 2rem;
}

a {
    font-family: 'Fraunces', serif;
}